import React from 'react';
import './login.scss';

export default class Login extends React.Component {
  constructor(props){
    super(props)
    this.state={

    }
  }
  render() {
    return (
      <div id="login">
        <div className="title">
          您的MetaMask被锁了
        </div>
        <div className="subtitle">
          只需打开MetaMask并按照说明进行解锁即可。
        </div>
        <div className="thumb">
          <img src="https://data.ehometd.com/files/0/1003/2.jpg?v=77975" alt=""/>
        </div>
        <div className="btn">
          <div className="text">安装</div>
        </div>
        <div className="tip">
          <div className="tiptitle">
            新手上路
          </div>
          <div className="content">
            <p>一台运行Chrome或Firefox桌面版本的计算机或笔记本电脑</p>
            <p>MetaMask,专门与网页应用共同使用的数字钱包</p>
            <p>以太币,为谜恋猫提供支持的数字支付形式</p>
          </div>
        </div>
      </div>
    )
  }
}


