import React from 'react';
import './home.scss';

export default class Home extends React.Component {
  constructor(props){
    super(props)
    this.state={

    }
  }
  render() {
    return (
      <div id="home">
        <div className="thumb">
          <img src="https://data.ehometd.com/files/0/1003/1.jpg?v=77975" width="100%" alt=""/>
        </div>
        <div className="words">
          <div className="title">
            2018 FIFA WORLD CUP
          </div>
          <div className="subtitle">
            第21届世界杯足球赛，将于2018年6月14日至7月15日在俄罗斯境内11座城市中的12座球场内举行
          </div>
          <div className="division">
            <div className="line"/>
            <div className="text">
              <div className="quan"/>
            </div>
            <div className="line"/>
          </div>
          <div className="content">
            <div className="text">这是世界杯首次在俄罗斯境内举行，也是世界杯首次在东欧国家举行，
              同时是继2006年德国世界杯后世界杯再一次在欧洲举行</div>
            <div className="text">俄罗斯世界杯共有32支球队参加比赛；
              除去东道主俄罗斯自动获得参赛资格以外，其他31支球队将通过参加预选赛获得参赛资格。</div>
            <div className="text">俄罗斯；沙特；埃及	；乌拉圭；葡萄牙；西班牙；摩洛哥；伊朗；法国；澳大利亚；秘鲁；丹麦</div>
            <div className="text">阿根廷；冰岛；克罗地亚；尼日利亚；巴西；瑞士；哥斯达黎加；塞尔维亚；德国；墨西哥；瑞典；韩国</div>
            <div className="text">比利时；巴拿马；突尼斯；英格兰；波兰；塞内加尔；哥伦比亚；日本</div>
          </div>
          <div className="btn">
            评分
          </div>
        </div>
      </div>
    )
  }
}


