import React from 'react';
import {Link} from 'react-router-dom'

export default class Head extends React.Component {
  constructor(props){
    super(props)
    this.state={
      active:{
        color:'#f5a623',
      },
      path:window.location.pathname,
    }
  }
  componentWillReceiveProps(props){
    this.setState({path:window.location.pathname})
  }
  render() {
    return (
      <div id="head">
        <div className="left">
          <div className="logo">
            logo
          </div>
          <div className="internet">
            网络状态
            <div className="quan"/>
          </div>
        </div>
        <div className="alljump">
          <span className="jump">
            {this.state.path==='/'?
            <Link to="/" style={this.state.active}>首页</Link>:
            <Link to="/" >首页</Link>
            }
          </span>
          <span className="jump">
            {this.state.path === '/news' ?
            <Link to="/news" style={this.state.active}>新闻</Link> :
            <Link to="/news">新闻</Link>
            }
          </span>
          <span className="jump">
           {this.state.path === '/score' ?
             <Link to="/score" style={this.state.active}>球员评分</Link>:
             <Link to="/score" >球员评分</Link>
           }
          </span>
          <span className="jump">
           {this.state.path === '/operationrecord' ?
             <Link to="/operationrecord" style={this.state.active}>操作记录</Link>:
             <Link to="/operationrecord" >操作记录</Link>
           }
          </span>
          <span className="jump">
           {this.state.path === '/operationnotice' ?
             <Link to="/operationnotice" style={this.state.active}>操作说明</Link>:
             <Link to="/operationnotice" >操作说明</Link>
           }
          </span>
          <span className="jump">
            {this.state.path === '/login' ?
              <Link to="/login" style={this.state.active}>登录</Link>:
              <Link to="/login" >登录</Link>
            }
          </span>
        </div>
      </div>
    )
  }
}


