import React from 'react';
import Centerspace from './center.js'
import Rightspace from './right.js'
import Dialogspace from './dialog.js'
import './score.scss';

export default class Score extends React.Component {
  constructor(props){
    super(props)
    this.state={
      Country:[
        {name:'巴西',thumb:'https://data.ehometd.com/files/0/1003/bx.jpg?v=77975',id:1},
        {name:'俄罗斯',thumb:'https://data.ehometd.com/files/0/1003/els.jpg?v=77975',id:2},
        {name:'巴西',thumb:'https://data.ehometd.com/files/0/1003/bx.jpg?v=77975',id:3},
        {name:'俄罗斯',thumb:'https://data.ehometd.com/files/0/1003/els.jpg?v=77975',id:4},
        {name:'巴西',thumb:'https://data.ehometd.com/files/0/1003/bx.jpg?v=77975',id:5},
        {name:'俄罗斯',thumb:'https://data.ehometd.com/files/0/1003/els.jpg?v=77975',id:6},
        {name:'巴西',thumb:'https://data.ehometd.com/files/0/1003/bx.jpg?v=77975',id:7},
        {name:'俄罗斯',thumb:'https://data.ehometd.com/files/0/1003/els.jpg?v=77975',id:8},
        {name:'巴西',thumb:'https://data.ehometd.com/files/0/1003/bx.jpg?v=77975'},
        {name:'俄罗斯',thumb:'https://data.ehometd.com/files/0/1003/els.jpg?v=77975'},
        {name:'巴西',thumb:'https://data.ehometd.com/files/0/1003/bx.jpg?v=77975'},
        {name:'俄罗斯',thumb:'https://data.ehometd.com/files/0/1003/els.jpg?v=77975'},
      ],
      peoples:[
        {name:'奥斯卡',thumb:'https://data.ehometd.com/files/0/1003/奥斯卡.jpg?v=77975',
          score:true,fraction:4.8,id:1},
        {name:'奥斯卡',thumb:'https://data.ehometd.com/files/0/1003/奥斯卡2.jpg?v=77975',
          score:true,fraction:4.8,id:2},
        {name:'奥斯卡',thumb:'https://data.ehometd.com/files/0/1003/奥斯卡3.jpg?v=77975',
          score:true,fraction:4.8,id:3},
        {name:'维克托',thumb:'https://data.ehometd.com/files/0/1003/维克托.jpg?v=77975',
          score:false,fraction:4.8,id:4},

      ],
      infomations:[
        {name:'白色奢华',content:'支持前锋内马尔！！！'},
        {name:'无人及妳',content:'投巴西，相信我~'},
        {name:'爱心小博士',content:'666666'},
        {name:'一世繁华',content:'就不就不，咋地'},
        {name:'小寒',content:'奥斯卡,好帅'},
        {name:'飞马',content:'奥斯卡！奥斯卡！'},
        {name:'涵',content:'维克托~'},
      ],
      listtype:true,
      active:{
        background:'rgba(172,132,84,0.6)'
      },
      activesort:{
        color:'#ac8454'
      },
      selectcountry:1,
      selectPlayer:1,
      sort:true,
      dialog:true,
      playerinfo:{
        pic:'https://data.ehometd.com/files/0/1003/奥斯卡.jpg?v=77975',
        thumb:[
          'https://data.ehometd.com/files/0/1003/奥斯卡.jpg?v=77975',
          'https://data.ehometd.com/files/0/1003/奥斯卡2.jpg?v=77975',
          'https://data.ehometd.com/files/0/1003/奥斯卡3.jpg?v=77975',
        ],
        name:'奥斯卡',
        Ename:'Oscar',
        Country:'巴西',
        birday:'1991-9-9',
        height:'181cm',
        weight:'66kg',
        alias:'奥斯卡,新卡卡',
        team:'上海上港足球俱乐部',
        place:'攻击性中场,前腰,边锋',
        teacher:'罗纳尔多',
        exp:[
          '1991年9月9日出生于巴西圣保罗州，巴西足球运动员，司职前腰。' +
          '现效力于中超联赛上海上港足球俱乐部。',
          '2009年，17岁的奥斯卡首次代表圣保罗队在职业比赛出场。' +
          '同年因与巴西球星卡卡有着相似的出道经历而被冠以了新卡卡的绰号。',
          '2010年6月，奥斯卡以475万英镑打破巴甲记录的转会费加盟巴西国际队。' +
          '随后奥斯卡帮助球队夺得了州冠军。',
          '2011年9月，在巴西0:0阿根廷的比赛中替补登场，完成了国家队首秀。',
          '2012年7月，奥斯卡以2500万英镑的转会费加盟英超豪门切尔西。 ' +
          '8月，奥斯卡代表巴西参加伦敦奥运会并获得奥运亚军。',
          '2013年，与巴西队一同夺得联合会杯冠军。',
          '2014年6月，在巴西世界杯揭幕战东道主巴西3:1克罗地亚' +
          '的比赛中获得了世界杯的首秀和首球。',
          '2015年3月，帮助切尔西队获得加盟后的第一座国内比赛奖杯联赛杯。' +
          '5月，再次帮助切尔西队获得了14/15赛季英超联赛冠军。',
          '在效力于切尔西的4年时间里，奥斯卡在各项赛事中为球队出场203次，' +
          '打进38球，获得了一座英超联赛的冠军、一座欧联杯的冠军和一座联赛杯的冠军。',
          '北京时间2016年12月23日，俱乐部正式宣布成功引进切尔西大将奥斯卡，' +
          '下赛季开始，奥斯卡将身披上港战袍，与球队一起征战。',
        ],
      },
      personalscore:{
        overallscore:'100.00',
        ability:'100.00',
        fuse:'100.00',
        plus:'100.00',
        keep:'100.00',
        state:'100.00',
      }
    }
  }
  changelisttype =()=>{
    this.setState((res)=>({
      listtype:!res.listtype
    }))
  }
  changecountry=(value)=>{
    this.setState({
      selectcountry:value,
    })
  }
  changeselectPlayer=(value)=>{
    this.setState({
      selectPlayer:value,
    })
  }
  changesort=()=>{
    this.setState((res)=>({
      sort:!res.sort,
    }))
  }
  changescore=(event)=>{
    event.persist();
    var value=event.currentTarget.value
    var name=event.currentTarget.name
    // this.setState(Object.assign({}, this.state.personalscore, {[name]:value}))
    let data=this.state.personalscore;
    data[name]=value;
    this.setState({data});
  }
  scoreplayer=(event)=>{
    event.stopPropagation();
    this.setState((res)=>({
      dialog:!res.dialog
    }))
  }
  render() {
    return (
      <div id="score">
        {this.state.dialog?
        <div className="nodialog">
          <Leftspace country={this.state.Country}
                     listtype={this.state.listtype}
                     active={this.state.active}
                     change={this.changelisttype}
                     selectcountry={this.state.selectcountry}
                     changecountry={this.changecountry}/>
          <Centerspace peoples={this.state.peoples}
                       selectplayer={this.state.selectPlayer}
                       active={this.state.active}
                       activesort={this.state.activesort}
                       changeplayer={this.changeselectPlayer}
                       sort={this.state.sort}
                       changesort={this.changesort}
                       scoreplayer={this.scoreplayer}
          />
          <Rightspace infos={this.state.infomations }/>
        </div>:
        <Dialogspace playerinfo={this.state.playerinfo}
                     personalscore={this.state.personalscore}
                     changescore={this.changescore}
                     scoreplayer={this.scoreplayer}/>
        }
      </div>
    )
  }
}

function Leftspace(props){
  const country=props.country;
  const listtype=props.listtype;
  const active=props.active
  const changeevent=props.change
  const selectcountry=props.selectcountry
  const changecountry=props.changecountry
  const listitems=country.map((value,index)=>
    <div className="country" key={index}>
      {selectcountry!==value.id?
      <div className="countryinfo" onClick={changecountry.bind(this,value.id)}>
        <div className="thumb">
          <img src={value.thumb} alt="" width="100%"/>
        </div>
        <div className="name">{value.name}</div>
      </div>:
      <div className="countryinfo2">
        <div className="thumb">
          <img src={value.thumb} alt="" width="100%"/>
        </div>
        <div className="name">{value.name}</div>
      </div>
      }
    </div>
  )
  const listitems2=country.map((value,index)=>
    <div className="country2" key={index}>
      {selectcountry !== value.id ?
        <div className="countryinfo" onClick={changecountry.bind(this, value.id)}>
          <div className="thumb2">
            <img src={value.thumb} alt="" width="100%"/>
          </div>
          <div className="name2">{value.name}</div>
        </div> :
        <div className="countryinfo2">
          <div className="thumb2">
            <img src={value.thumb} alt="" width="100%"/>
          </div>
          <div className="name2">{value.name}</div>
        </div>
      }
    </div>
  )
  return(
    <div className="left">
      <div className="header">
        <div className="search">
          <input />
          <div className="icon">
            <img src="https://data.ehometd.com/files/0/1003/搜索.png?v=77975" alt="" width="30px"/>
          </div>
        </div>
        <div className="specialbtn">
          {listtype?
          <div className="btn" style={active}>
            <img src="https://data.ehometd.com/files/0/1003/图片列表.png?v=77975" alt="" width="100%"/>
          </div>:
          <div className="btn" onClick={changeevent}>
            <img src="https://data.ehometd.com/files/0/1003/图片列表.png?v=77975" alt="" width="100%"/>
          </div>
          }
          {!listtype?
          <div className="btn" style={active}>
            <img src="https://data.ehometd.com/files/0/1003/列表.png?v=77975" alt="" width="100%"/>
          </div>:
          <div className="btn" onClick={changeevent}>
            <img src="https://data.ehometd.com/files/0/1003/列表.png?v=77975" alt="" width="100%"/>
          </div>
          }
          <div className="btn">
            <img src="https://data.ehometd.com/files/0/1003/刷新.png?v=77975" alt="" width="100%"/>
          </div>
        </div>
      </div>
      {listtype?listitems:listitems2}
    </div>
  )
}



