import React from 'react';


export default function Right(props){
  const infomations=props.infos;
  const listitems=infomations.map((value,index)=>
    <div className="infos" key={index}>
      {value.name}:{value.content}
    </div>
  )
  return(
    <div className="right">
      <div className="title">
        系统消息：本站提醒禁止发送违法违规不良信息，请勿轻信各类虚假或广告信息，以免上当受骗。
      </div>
      <div className="content">
        {listitems}
      </div>
      <div className="report">
        <div className="text">
          我要评论
        </div>
      </div>
    </div>
  )
}


